/* styles.css */
body {
  font-family: sans-serif;
  background-color: #fff;
  background-size: cover;
  height: 100%;
  width: 100%;
  margin: 10%;
  color: #000;
  overflow-x: hidden;
}
.portfolio {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 20px;
  font-family: "Arial", sans-serif;
  height: 100%;
  width: 100%;
}

@media (max-width: 768px) {
  #portfolio {
    /* Adjust styles for mobile */
    overflow-x: hidden;
    margin-bottom: 3rem;
  }
}
@media (max-width: 768px) {
  .portfolio {
    /* Adjust styles for mobile */
    overflow-x: hidden;
  }
}
.intro {
  margin-bottom: 40px;
}

.profile-image {
  background-color: #000;
  width: 200px;
  height: 200px;
  border-radius: 80%;
  object-fit: cover;
  margin-bottom: 20px;
}

.portfolio-title {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 10px;
}

.portfolio-intro {
  font-size: 18px;
}

.socials {
  margin-bottom: 40px;
}

.section-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.social-links {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
}

.social-links li {
  margin: 0 10px;
}

.social-links li a {
  color: #000;
  text-decoration: none;
  font-size: 18px;
}

.projects {
  display: flex;
  align-items: center;
  column-gap: 1px;
  overflow-x: scroll;
  /* scroll-snap-type: all mandatory; */
  scroll-behavior: smooth;
  margin-bottom: 40px;
  padding: 2px;
}

.projects {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  overflow: hidden;
}

.carousel-container {
  padding: 15%;
  width: 70%;
  overflow: hidden;
  position: relative;
}

.carousel-track {
  display: flex;
  width: 100%;
  transition: transform 0.3s ease-in-out;
}

.carousel-slide {
  flex: 0 0 100%;
  padding: 0 20px;
}

.project {
  padding: 0px;
  height: 70%;
  text-align: center;
}

.project-description {
  font-size: 1.5rem;
  font-weight: bold;
}

.project-link {
  text-decoration: none;
  color: #333;
}

.connect {
  border-radius: 5px;
  max-width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.carousel-controls {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.prev-button,
.next-button {
  border: none;
  padding: 8px 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin: 0px 20px;
  border-color: #000 5px solid;
}

.prev-button:hover,
.next-button:hover {
  background-color: #fff;
  color: #000;
}

@media (max-width: 768px) {
  .projects {
    margin-top: -230px;
  }
  .carousel-container {
    width: 80%; /* Adjust width for smaller screens */
  }

  .carousel-slide {
    padding: 0; /* Remove padding between slides for mobile */
  }

  .project {
    padding: 10px; /* Reduce padding for smaller screens */
  }

  .connect {
    max-width: 40%; /* Reduce image width for mobile */
    margin: 0 auto; /* Center images */
  }

  .project-description {
    font-size: 1.2rem; /* Adjust font size for project description */
  }

  .carousel-controls {
    margin-top: 10px; /* Adjust top margin for spacing */
  }
}

.resume {
  text-decoration: none;
  color: #000;
  font-size: 20px;
  padding-left: 20%;
  margin-top: 30px;
}
.btn {
  background-color: #fff;
  height: 30px;

  border-radius: 6px;
  color: black;
  text-decoration: none;
  text-align: center;
  font-size: 15px;
  margin-left: 20%;
}
.btn a {
  text-decoration: none;
  color: black;
}
.btn:hover {
  background: black;
  border: 1px solid;
  color: #fff;
}
.btn a:hover {
  color: #fff;
}
@keyframes appear {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

.animated-text {
  display: flex;
  overflow: hidden;
  white-space: nowrap;
  animation: typing 2s steps(40, end) both;
}

.connect {
  width: 100%;
  height: 100%;
  border: #000 solid 2px;
}
.navbar {
  display: flex;
  margin-top: 15%;
}
hr {
  width: 600px;
  height: 2px;
  background-color: #000;
  border: none;
  margin-top: 10px;
}
.button {
  padding: 1.3em 3em;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  font-weight: 500;
  color: #000;
  background-color: #fff;
  border: none;
  border-radius: 45px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease 0s;
  cursor: pointer;
  outline: none;
  margin-top: 20px;
}

.button:hover {
  background-color: #000;
  box-shadow: 0px 15px 20px rgba(0, 0, 0, 0);
  color: #fff;
  transform: translateY(-7px);
}

.button:active {
  transform: translateY(-1px);
}
.menu-icon {
  display: block;
  cursor: pointer;
}

.sidebar {
  position: fixed;
  left: -100%;
  top: 0;
  width: 60%;
  height: 100%;
  border-right: 1px solid #444;
  background-color: #000300;
  transition: left 0.5s ease-in-out;
}

.sidebar h1 {
  font-size: 24px;
  font-weight: bold;
  color: #218b6a;
  margin: 20px;
}

.sidebar ul {
  padding: 20px;
  text-transform: uppercase;
  border-bottom: 1px solid #444;
}

.sidebar ul li {
  padding: 10px;
  border-bottom: 1px solid #444;
}

/* styles.css */

/*===== GOOGLE FONTS =====*/
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;700&display=swap");

/*===== VARIABLES CSS =====*/
:root {
  --header-height: 3rem;
  --font-medium: 500;
}

/*===== Colores =====*/
:root {
  --first-color: #8d8480;
  --white-color: #fafaff;
  --dark-color: #000;
  --text-color: #000;
  --outline-color: #fff;
}

/*===== Fuente y tipografia =====*/
:root {
  --body-font: "Montserrat", sans-serif;
  --big-font-size: 6.25rem;
  --h2-font-size: 1.25rem;
  --normal-font-size: 0.938rem;
  --small-font-size: 0.813rem;
}
@media screen and (min-width: 768px) {
  :root {
    --big-font-size: 10.5rem;
    --h2-font-size: 2rem;
    --normal-font-size: 1rem;
    --small-font-size: 0.875rem;
  }
}

/*===== Margenes =====*/
:root {
  --mb-1: 0.5rem;
  --mb-2: 1rem;
  --mb-3: 1.5rem;
  --mb-4: 2rem;
}

/*===== z index =====*/
:root {
  --z-fixed: 100;
}

/*===== BASE =====*/
*,
::before,
::after {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
body {
  margin: var(--header-height) 0 0 0;
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  font-weight: var(--font-medium);
  color: var(--text-color);
  line-height: 1.6;
}
h1,
h2,
p {
  margin: 0;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  text-decoration: none;
  color: var(--text-color);
}
img {
  max-width: 100%;
  height: auto;
  display: block;
}

/*===== CLASS CSS ===== */
.section {
  padding: 3rem 0;
}
.section-title {
  position: relative;
  font-size: var(--h2-font-size);
  color: var(--dark-color);
  margin: var(--mb-4) 0;
  text-align: center;
}
.section-title::after {
  position: absolute;
  content: "";
  width: 32px;
  height: 0.18rem;
  left: 0;
  right: 0;
  margin: auto;
  top: 2rem;
  bottom: 2rem;
  background-color: var(--first-color);
}

/*===== LAYOUT =====*/
.bd-grid {
  max-width: 1024px;
  display: grid;
  grid-template-columns: 100%;
  grid-column-gap: 2rem;
  width: calc(100% - 2rem);
  margin-left: var(--mb-2);
  margin-right: var(--mb-2);
}
.l-header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
  background-color: black;
  color: #fff;
}

/*===== NAV =====*/
.nav {
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 768px) {
  .nav__menu {
    position: fixed;
    top: var(--header-height);
    left: -100%;
    width: 80%;
    height: 100%;
    padding: 2rem;
    background-color: #000;
    transition: 0.5s;
    webkit-backdrop-filter: #000;
    color: #fff;
    align-items: left;
  }
}
.nav__item {
  place-content: left;
  place-items: left;
  place-self: left;
  margin-bottom: var(--mb-4);
  outline-color: #fff 10px solid;
}
.nav__link {
  outline-color: #fff 10px solid;
  position: relative;
  color: #fff;
  align-items: left;
}
.nav__link:hover {
  color: var(--first-color);
}
.nav__logo {
  color: var(--white-color);
}
.nav__toggle {
  color: var(--white-color);
  font-size: 1.5rem;
  cursor: pointer;
}

/*=== Show menu ===*/
.show {
  left: 0;
}

/*Active menu*/
.active::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 0.18rem;
  left: 0;
  top: 2rem;
  background-color: var(--first-color);
}

/*===== HOME =====*/
.home {
  position: relative;
  background-color: var(--first-color);
  overflow: hidden;
}
.home__container {
  height: calc(100vh - var(--header-height));
  row-gap: 5rem;
}
.home__title {
  align-self: flex-end;
  font-size: var(--big-font-size);
  color: var(--white-color);
  line-height: 0.8;
}
.home__title span {
  text-shadow: 0 20px 25px rgba(0, 0, 0, 0.5);
}
.home__scroll {
  align-self: flex-end;
  padding-bottom: var(--mb-4);
}
.home__scroll-link {
  transform: rotate(-180deg);
  color: var(--white-color);
}
.home__img {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 295px;
}

/* ===== ABOUT =====*/
.about__container {
  justify-items: center;
  row-gap: 2rem;
  text-align: center;
}
.about__img {
  display: flex;
  justify-content: center;
  padding-top: 0rem;
  width: 120px;
  height: 120px;
  background-color: var(--first-color);
  border-radius: 50%;
  overflow: hidden;
  max-width: fit-content;
}
.about__img img {
  width: 100px;
}
.about__subtitle {
  font-size: var(--h2-font-size);
  color: var(--first-color);
  margin-bottom: var(--mb-1);
}
.about__text {
  margin-bottom: var(--mb-4);
}
.about__profession {
  display: block;
  margin-bottom: var(--mb-4);
}
.about__social-icon {
  font-size: 1.4rem;
  margin: 0 var(--mb-1);
}
.about__social-icon:hover {
  color: var(--first-color);
}

/* ===== SKILLS =====*/

.skills__container {
  row-gap: 2rem;
  display: flex;
  justify-content: center;
  padding: 15%;
}
.skills__subtitle {
  color: #000;
  margin-bottom: var(--mb-3);
}

.skills__name {
  display: inline-flex;
  font-size: var(--small-font-size);
  margin-right: var(--mb-2);
  margin-bottom: var(--mb-3);
  padding: 0.35rem 0.2rem;
  background-color: var(--white-color);
  border-radius: 0.25rem;
  width: 28%;
  flex-direction: column;
  align-items: center;
  text-align: center;
  line-height: 1.2;
  max-height: 5rem; /* Adjust as needed to control the number of lines */
  overflow: hidden;
  white-space: normal;
}

.skills__img img {
  border-radius: 0.5rem;
}
.skills__box {
  background-color: var(--white-color);
  border-radius: 0.5rem;
  padding: 1rem;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transition: 0.3s;
}
/* ===== PORTFOLIO =====*/
.portfolio {
  background-color: var(--white-color);
}
.portfolio__container {
  justify-items: center;
  row-gap: 2rem;
}
.portfolio__img {
  position: relative;
  overflow: hidden;
}
.portfolio__img img {
  border-radius: 0.5rem;
}
.portfolio__link {
  position: absolute;
  bottom: -100%;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 0.5rem;
  cursor: pointer;
  webkit-backdrop-filter: blur(10px);
  transition: 0.3s;
}
.portfolio__img:hover .portfolio__link {
  bottom: 0;
}
.portfolio__link-name {
  color: var(--dark-color);
}

/* ===== CONTACT =====*/
.contact__container {
  row-gap: 2rem;
}
.contact__subtitle {
  font-size: var(--normal-font-size);
  color: var(--first-color);
}
.contact__text {
  display: inline-block;
  margin-bottom: var(--mb-2);
}
.contact__inputs {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 1rem;
}
.contact__input {
  width: 100%;
  padding: 0.8rem;
  outline: none;
  border: 1.5px solid var(--dark-color);
  font-size: var(--normal-font-size);
  margin-bottom: var(--mb-4);
  border-radius: 0.5rem;
}
.contact__button {
  display: block;
  background-color: var(--first-color);
  color: var(--white-color);
  padding: 0.75rem 2.5rem;
  margin-left: auto;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  font-size: var(--normal-font-size);
  cursor: pointer;
}

/* ===== FOOTER=====*/
.footer {
  background-color: var(--dark-color);
}
.footer__container {
  row-gap: 2rem;
}
.footer__title {
  font-size: var(--normal-font-size);
  color: var(--white-color);
  margin-bottom: var(--mb-2);
}
.footer__link {
  padding: 0.25rem 0;
}
.footer__link:hover {
  color: var(--first-color);
}
.footer__social {
  font-size: 1.4rem;
  margin-right: var(--mb-1);
}
.footer__social:hover {
  color: var(--first-color);
}

/* ===== MEDIA QUERIES =====*/
/* For small devices */
@media screen and (max-width: 320px) {
  .home__img {
    width: 230px;
  }
}

@media screen and (min-width: 768px) {
  body {
    margin: 0;
  }
  .section {
    padding-top: 4rem;
  }
  .section-title {
    margin-bottom: 3rem;
  }
  .section-title::after {
    width: 64px;
    top: 3rem;
  }

  .nav {
    height: calc(var(--header-height) + 1rem);
  }
  .nav__list {
    display: flex;
  }
  .nav__item {
    margin-left: var(--mb-4);
    margin-bottom: 0;
  }
  .nav__toggle {
    display: none;
  }
  .nav__link {
    color: var(--white-color);
  }
  .nav__link:hover {
    color: var(--white-color);
  }
  .active::after {
    background-color: var(--white-color);
  }

  .home__container {
    height: 100vh;
    grid-template-rows: 1.7fr 1fr;
    row-gap: 0;
  }
  .home__img {
    width: 524px;
    right: 10%;
  }

  .about__container {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    text-align: initial;
    padding: 4rem 0;
  }
  .about__img {
    width: 200px;
    height: 200px;
  }
  .about__img img {
    width: 165px;
  }

  .skills__container {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }

  .portfolio__container {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    column-gap: 2rem;
  }

  .contact__container {
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }
  .contact__form {
    width: 380px;
  }

  .footer__container {
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
  }
}

@media screen and (min-width: 1024px) {
  .bd-grid {
    margin-left: auto;
    margin-right: auto;
  }
}

/* For tall screens on mobiles y desktop*/
@media screen and (min-height: 721px) {
  .home__container {
    height: 640px;
  }
  .home__img {
    width: 500px;
    right: 24%;
  }
}
